import React from 'react'

import Img from 'gatsby-image'
import projectStyle from './projects.module.css'
import { graphql, useStaticQuery } from 'gatsby';
import Fade from 'react-reveal/Fade'

const Project = () => {

    const projectQuery = useStaticQuery ( graphql`
        query {
            allProjectsJson {
                edges {
                    node {
                        name
                        about
                        role
                        description
                        url
                        urlLabel
                        image {
                            label
                            src {
                                childImageSharp {
                                    fluid (maxWidth: 1080) {
                                        ...GatsbyImageSharpFluid
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    `)

    return (
        <Fade>
            <div className={projectStyle.row}>
                {
                    /**
                     * Mapping each of the object to the JSX
                    */
                    projectQuery.allProjectsJson.edges.map((edge, index) => {
                        const projectItem = edge.node
                        var oddClasses = (index % 2 !== 0) ?
                            projectStyle.aboutCont + ' ' + projectStyle.first : projectStyle.aboutCont
                        
                        return (
                            <div key={index} className={projectStyle.main}>
                                {/*
                                    Checking if the NAME is NOT of TYPE YEAR
                                    If true, render project JSX, else don't
                                */}
                                {!/\d{4}/.test(projectItem.name) ? 
                                    <div className={projectStyle.projCont}>
                                        <div className={oddClasses}>

                                            <h2>{projectItem.name}</h2>
                                            <h4>{projectItem.about}</h4>
                                            <div>
                                                { projectItem.role.split(',').map(element => {
                                                        return (
                                                            <p className={projectStyle.role}>{element}</p>
                                                        )
                                                })}
                                            </div>
                                            <h3>{projectItem.description}</h3>

                                            {
                                                <a href={projectItem.url} target="_blank" rel="noopener noreferrer"><button className={projectStyle.button}>{projectItem.urlLabel}</button></a>
                                            }
                                            
                                        </div>

                                        <div className={projectStyle.imgCont}>
                                            {
                                                !!projectItem.image.src && !!projectItem.image.src.childImageSharp
                                                    ? <Img fluid={projectItem.image.src.childImageSharp.fluid}
                                                        alt={projectItem.image.label} />
                                                    : <img src={projectItem.image.src.publicURL} alt={projectItem.image.label} />
                                                
                                            }
                                        </div>
                                    </div> :

                                    <div className={projectStyle.yearCont}>
                                        <span>
                                            {projectItem.name}
                                        </span>
                                    </div>
                                }
                            </div>
                        )
                    })
                }
            </div>
        </Fade>
    )
}

export default Project